import React from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import AnchorLink from "react-anchor-link-smooth-scroll";
const Navbar: React.FC = () => {
  return (
    <>
      <AppBar color="default" position="fixed" style={{ alignItems: "center" }}>
        <Toolbar>
          <AnchorLink
            href={"#about"}
            style={{
              textDecoration: "none",
              color: "inherit"
            }}
          >
            <Button>ABOUT</Button>
          </AnchorLink>
          <AnchorLink
            href={"#skills"}
            style={{
              textDecoration: "none",
              color: "inherit"
            }}
          >
            <Button>SKILLS</Button>
          </AnchorLink>
          <AnchorLink
            href={"#works"}
            style={{
              textDecoration: "none",
              color: "inherit"
            }}
          >
            <Button>WORKS</Button>
          </AnchorLink>
          <AnchorLink
            href={"#contact"}
            style={{
              textDecoration: "none",
              color: "inherit"
            }}
          >
            <Button>CONTACT</Button>
          </AnchorLink>
        </Toolbar>
      </AppBar>
      <span style={{ display: "block", height: "50px", width: "100%" }} />
    </>
  );
};

export default Navbar;
